<template>
  <div class="root">
   <div id="container" ref="container"></div>
    <div class="container-shell" style="display:none">
     <!--开始停止操作-->
      <div class="input" >
        <button v-if="!playing" @click="play">播放</button>
        <button v-else @click="pause">停止</button>
      </div>
      <!--其它操作-->
      <div class="input" v-if="loaded" style="line-height: 30px;">
        <button @click="destroy">销毁</button>
        <button v-if="quieting" @click="cancelMute">取消静音</button>
        <template v-else>
          <button @click="mute">静音</button>
          音量
          <select v-model="volume" @change="volumeChange">
            <option value="1">100</option>
            <option value="0.75">75</option>
            <option value="0.5">50</option>
            <option value="0.25">25</option>
          </select>
        </template>
        <span>旋转</span>
        <select v-model="rotate" @change="rotateChange">
          <option value="0">0</option>
          <option value="90">90</option>
          <option value="270">270</option>
        </select>

        <button @click="fullscreen">全屏</button>
        <button @click="screenShot">截图</button>
        <div style="line-height: 30px">
          <input
              type="checkbox"
              ref="operateBtns"
              v-model="showOperateBtns"
              @change="restartPlay"
          /><span>操作按钮</span>
          <input
              type="checkbox"
              ref="operateBtns"
              v-model="showBandwidth"
              @change="restartPlay"
          /><span>网速</span>
          <span v-if="performance">性能：{{ performance }}</span>
        </div>
      </div>
      <!--缓冲和渲染模式-->
      <div class="option" style="display:none">
        <span>缓冲(秒):</span>
        <input
            style="width: 50px"
            type="number"
            ref="buffer"
            value="0.2"
            @change="changeBuffer"
        />
        <!-- <input
          type="checkbox"
          v-model="vod"
          ref="vod"
          @change="restartPlay"
        /><span>点播模式</span> -->
        <input
            type="checkbox"
            ref="offscreen"
            v-model="forceNoOffscreen"
            @change="restartPlay"
        /><span>禁用离屏渲染</span>
        <input type="checkbox" ref="resize" @change="changeResize"/><span
      >禁止画面拉伸</span
      >
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "DemoPlayer",
  props: {
      playUrl:String
  },
  data() {
    return {
      liveplay: null,
      wasm: false,
      vc: "ff",
      playing: false,
      quieting: false,//播放器是否静音
      loaded: true, // mute //播放器默认是否为播放状态
      showOperateBtns: false, //播放器是否显示工具栏
      showBandwidth: false,//显示网速
      err: "",
      speed: 0,
      performance: "",
      volume: 10,
      rotate: 0,
      vod: false,
      forceNoOffscreen: true,
    };
  },
  mounted() {
    this.create();
    window.onerror = (msg) => (this.err = msg);
    //开始播放
    //this.play();
  },
  unmounted() {
    this.liveplay.destroy();
  },
  methods: {
    create(options) {
      options = options || {};
      this.liveplay = new window.Liveplay(
          Object.assign(
              {
                container: document.querySelector("#container"),
                videoBuffer: Number(this.$refs.buffer.value), // 缓存时长
                isResize: false,
                text: "",
                // background: "bg.jpg",
                loadingText: "加载中",
                // hasAudio:false,
                debug: true,
                showBandwidth: this.showBandwidth, // 显示网速
                operateBtns: {
                  fullscreen: this.showOperateBtns,
                  screenshot: this.showOperateBtns,
                  play: this.showOperateBtns,
                  audio: this.showOperateBtns,
                },
                vod: this.vod,
                forceNoOffscreen: this.forceNoOffscreen,
                isNotMute: false,
              },
              options
          )
      );
      // this.liveplay.onLog = (msg) => console.log("onLog", msg);
      // this.liveplay.onLoad = (msg) => console.log("onLoad");
      // this.liveplay.onRecord = (msg) => console.log("onRecord", msg);
      // this.liveplay.onPause = () => console.log("onPause");
      // this.liveplay.onPlay = () => console.log("onPlay");
      // this.liveplay.onFullscreen = (msg) => console.log("onFullscreen", msg);
      // this.liveplay.onMute = (msg) => console.log("onMute", msg);
      // this.liveplay.onInitSize = () => console.log("onInitSize");
      var _this = this;
      this.liveplay.on("load", function () {
        console.log("on load");
      });
      this.liveplay.on("log", function (msg) {
        console.log("on log", msg);
      });
      this.liveplay.on("record", function (msg) {
        console.log("on record:", msg);
      });
      this.liveplay.on("pause", function () {
        console.log("on pause");
        _this.playing = false;
      });
      this.liveplay.on("play", function () {
        console.log("on play");
        _this.playing = true;
      });
      this.liveplay.on("fullscreen", function (msg) {
        console.log("on fullscreen", msg);
        this.forceNoOffscreen=msg
                //alert("点击了全屏"+msg)
      });
      this.liveplay.on("mute", function (msg) {
        console.log("on mute静音了", msg);
        _this.quieting = msg;
      });
      this.liveplay.on("audioInfo", function (msg) {
        console.log("audioInfo", msg);
      });
      this.liveplay.on("bps", function (bps) {
         console.log('bps', bps);
      });
      let _ts = 0;
      this.liveplay.on("timeUpdate", function (ts) {
         console.log('timeUpdate,old,new,timestamp', _ts, ts, ts - _ts);
        _ts = ts;
      });
      this.liveplay.on("videoInfo", function (info) {
        console.log("videoInfo", info);
      });
      this.liveplay.on("error", function (error) {
        console.log("error", error);
      });
      this.liveplay.on("timeout", function () {
        console.log("timeout");
      });
      this.liveplay.on('start', function () {
        console.log('start');
      })
      // this.liveplay.on("stats", function (stats) {
      //   console.log('stats', JSON.stringify(stats));
      // });
      this.liveplay.on("performance", function (performance) {
        var show = "卡顿";
        if (performance === 2) {
          show = "非常流畅";
        } else if (performance === 1) {
          show = "流畅";
        }
        _this.performance = show;
      });
      this.liveplay.on('buffer', function (buffer) {
        console.log('buffer', buffer);
      })
      this.liveplay.on('stats', function (stats) {
        console.log('stats', stats);
      })
      this.liveplay.on('kBps', function (kBps) {
        console.log('kBps', kBps);
      });
      // 显示时间戳 PTS
      this.liveplay.on('videoFrame', function () {
      })
      //
      this.liveplay.on('metadata', function () {
      });
      // console.log(this.liveplay);
    },
    play() {
      // this.liveplay.onPlay = () => (this.playing = true);
      this.liveplay.on("play", () => {
        this.playing = true;
        this.loaded = true;
        this.liveplay.quieting=this.quieting;
      });
      if (this.playUrl) {
        if (this.liveplay.hasLoaded()) {
          this.liveplay.play(this.playUrl);
        } else {
          this.liveplay.on("load", () => {
            this.liveplay.play(this.playUrl);
          });
        }
      }
    },
    mute() {
      this.liveplay.mute();
    },
    cancelMute() {
      this.liveplay.cancelMute();
    },
    pause() {
      this.liveplay.pause();
      this.playing = false;
      this.err = "";
      this.performance = "";
    },
    volumeChange() {
      this.liveplay.setVolume(this.volume);
    },
    rotateChange() {
      this.liveplay.setRotate(this.rotate);
    },
    destroy() {
      if (this.liveplay) {
        this.liveplay.destroy();
      }
      this.create();
      this.playing = false;
      this.loaded = false;
      this.performance = "";
    },
    fullscreen() {
      this.liveplay.setFullscreen(true);
    },
    screenShot() {
      this.liveplay.screenshot();
    },
    changeWasm() {
      this.wasm = this.$refs.wasm.checked;
    },
    restartPlay() {
      this.destroy();
      this.play();
    },
    changeBuffer() {
      this.liveplay.setBufferTime(Number(this.$refs.buffer.value));
    },
    changeResize() {
      const value = this.$refs.resize.checked ? 1 : 0;
      this.liveplay.setScaleMode(value);
    },
    changeVod() {
      const value = this.$refs.vod.checked ? 1 : 0;
      this.liveplay.setVod(value);
    },
    changeOffscreen() {
      const value = this.$refs.offscreen.checked ? 1 : 0;
      this.liveplay.setNoOffscreen(value);
    },
  },
};
</script>
