  background: rgba(13, 14, 27, 0.9);
  width: 520px;
  height: 360px;
}
.input {
  display: flex;
  margin-top: 10px;
  color: white;
  place-content: stretch;
}
.input2 {
  bottom: 0px;
}
.input input {
  flex: auto;
}
.err {
  position: absolute;
  top: 40px;
  left: 10px;
  color: red;
}
.option {
  position: absolute;
  top: 4px;
  right: 10px;
  display: flex;
  place-content: center;
  font-size: 12px;
}
.option span {
  color: white;
}
.page {
  background: url(/bg.jpg);
  background-repeat: no-repeat;
  background-position: top;
}
